import React from 'react';
import styled from 'styled-components';
import Tetris from 'react-tetris';


const Score = styled.div`
  position: relative;
  font-family: monospace;
  font-size: 18px;
  color: #888;
`;
const Popup = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  padding: 12px 24px;
  border-radius: 4px;
  text-align: center;
`;

const MINIMAL_PRICE = 1500;
const getRealPoinst = (points: number): number => {
    return points / 4;
}
const getNotification = (points: number): string => {
    if (points < 100) {
        return "Dost slabé, asi jako slimák na maratonu. Zkuste to znovu."
    }
    else if (points < 500) {
        return "I kaktus by měl lepší výsledky..."
    }
    else if (points < 1000) {
        return "Kdyby ještě žili dinosauři, tak by Vám řekli: sice dobré, ale buďte lepší"
    }
    else if (points < 1500) {
        return "Jste těsně před cílem!"
    }
    else {
        return "Gratuluju, vyhráváte cenu 1500Kč. Zadejte heslo a Pavel Vás zkontaktuje. 😁"
    }
}

const getButton = (points: number): string => {
    if (points < 1500) {
        return "Zkusit znovu"
    }
    return "Zahrát si znovu"
}

const GamePanel = (): JSX.Element => {
    const [heslo, setHeslo] = React.useState('');
    const [notification, setNotification] = React.useState('');
    const sendMail = (points: number) => {
        const apiURL = `https://script.google.com/macros/s/AKfycbze9XeoPhxIQjoWuuh4tXfxw8cjodP8ZJQMCj2tRoKVT0aW_yjo/exec?points=${points.toString()}&heslo=${heslo}`
        fetch(apiURL).then(() => { setNotification('Posláno') })
    }

    return (
        <Tetris>
            {({
                HeldPiece,
                Gameboard,
                PieceQueue,
                points,
                linesCleared,
                state,
                reset
            }: any) => (
                <div className='tetrix-wrapper'>
                    <div className='left-panel'>
                        <p>Body: <b>{getRealPoinst(points)}</b></p>
                        <p>Hotových řad: <b>{linesCleared}</b></p>
                        <p>Následující dílky:</p>
                        <PieceQueue />
                    </div>
                    <div className='right-panel'>
                        <Gameboard />
                        {state === 'LOST' &&
                            <div className='center-flex'>
                                <h2 className='h2'>{getNotification(getRealPoinst(points))}</h2>
                                {
                                    points >= 1500 &&
                                    <div className='center-flex'>
                                        <input type="text" placeholder="zadejte heslo" onChange={e => setHeslo(e.target.value)} />
                                        <p>{notification}</p>
                                        <button className='reset-button' onClick={() => sendMail(points)}>Odeslat</button>
                                    </div>
                                }
                                <button className='reset-button' onClick={() => {
                                    setHeslo('')
                                    setNotification('')
                                    reset();
                                }}>{getButton(getRealPoinst(points))}</button>
                            </div>
                        }
                    </div>
                </div>
            )
            }
        </Tetris >

    )
};

const Digit = styled.span`
  font-family: monospace;
  padding: 1px;
  margin: 1px;
  font-size: 24px;
`;

type DigitsProps = {
    children: number;
    count?: number;
};
const Digits = ({ children, count = 4 }: DigitsProps): JSX.Element => {
    let str = children.toString();

    while (str.length < count) {
        str = `${0}${str}`;
    }

    return (
        <>
            {str.split('').map((digit, index) => (
                <Digit key={index}>{digit}</Digit>
            ))}
        </>
    );
};

export default GamePanel;